export const getCurrentUser = () => {
    const users = JSON.parse(localStorage.getItem('users')) || [];
    const isLoggedInUser = JSON.parse(localStorage.getItem('isLoggedInUser')); //    
    if (!isLoggedInUser) return null; //     null 
    return users.find((user) => user.email === isLoggedInUser) || null;
};


//     
export const tryLogin = (email, password) => {
    const users = JSON.parse(localStorage.getItem('users')) || [];
    const user = users.find((u) => u.email === email && u.password === password);
    if (user) {
        localStorage.setItem('isLoggedIn', 'true');
        localStorage.setItem('isLoggedInUser', JSON.stringify(user.email)); //   
        return user;
    }
    return null;
};

export const tryRegister = (email, password) => {
    const users = JSON.parse(localStorage.getItem('users')) || [];
    if (users.some(u => u.email === email)) {
        return false;
    }
    users.push({ email, password });
    localStorage.setItem('users', JSON.stringify(users));
    return true;
};

export const isLoggedIn = () => {
    return localStorage.getItem('isLoggedIn') === 'true';
};


//   
export const logout = () => {
    localStorage.removeItem('isLoggedIn');
    localStorage.removeItem('currentUser'); // currentUser 
};
